.title {
  text-align: left;
  font-size: 2rem;
  font-weight: 800; }

.text {
  margin: 2rem 0rem;
  font-weight: 500;
  font-size: 1.25rem; }

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-30px); }
  60% {
    transform: translateY(-15px); } }

.projects {
  padding: 0;
  overflow: hidden; }

.project a {
  margin: 0; }

.popup-content {
  text-align: center; }

.outro {
  margin-top: 6rem; }

@media screen and (min-width: 576px) {
  .title {
    font-size: 3rem;
    margin-left: 0rem; }
  .text {
    margin-left: 0rem; }
  .scroll-down {
    margin-left: 0rem; }
  .outro {
    margin-top: 8rem; } }

@media screen and (min-width: 768px) {
  .title {
    font-size: 3rem; }
  .text {
    font-size: 1.3rem; }
  .homepage-row {
    margin-bottom: 1rem; }
  .outro {
    margin-top: 10rem; } }
