body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth; }

body {
  text-align: left;
  font-family: "Avenir", sans-serif;
  color: #3e3e3e; }

p {
  font-weight: 450; }

a:hover {
  text-decoration: none; }

.title {
  font-size: 3.5rem; }

.link {
  font-weight: 500;
  text-decoration: none;
  color: #545454; }

.link:hover {
  color: #4305f2; }

.text-link {
  color: #4305f2;
  font-weight: 600;
  -webkit-transition: color;
  transition: color;
  background-size: 100% 200%;
  background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(50%, transparent), color-stop(50%, #4305f2), to(#4305f2));
  background-image: -webkit-linear-gradient(bottom, transparent 0%, transparent 50%, #4305f2 50%, #4305f2 100%);
  background-image: linear-gradient(to top, transparent 0%, transparent 50%, #4305f2 50%, #4305f2 100%);
  background-position: 0 100%;
  will-change: background-position; }

.text-link:hover {
  -webkit-animation: backgroundAnimateIn 0.3s;
          animation: backgroundAnimateIn 0.3s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@-webkit-keyframes backgroundAnimateIn {
  0% {
    background-position: 0 -100%; }
  63% {
    background-position: 0 -95%; }
  86% {
    background-position: 0 2.5%; }
  100% {
    color: white;
    background-position: 0 0%; } }

@keyframes backgroundAnimateIn {
  0% {
    background-position: 0 -100%; }
  63% {
    background-position: 0 -95%; }
  86% {
    background-position: 0 2.5%; }
  100% {
    color: white;
    background-position: 0 0%; } }

.black {
  color: #000000; }

.light {
  font-weight: 300; }

.italic {
  font-style: italic; }

.container-hero {
  padding: 0; }

.hero {
  max-width: 100%;
  padding: 0; }

.arrow {
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
  height: 2rem;
  margin: 2rem 0rem;
  cursor: pointer; }

@media screen and (min-width: 576px) {
  .intro {
    margin: 8rem 0 2rem 0; }
  .title {
    font-size: 4rem; }
  .text-section {
    padding: 0; } }

@media screen and (min-width: 768px) {
  .intro {
    margin: 8rem 0 2rem 0; }
  .title {
    font-size: 3rem; } }

@media screen and (min-width: 1300px) {
  .intro {
    margin: 10rem 0 4rem 0; }
  .text-section {
    width: 100%; } }

