.content {
  margin: 2rem auto 4rem auto; }

.case-study-title {
  max-width: 667px;
  margin-left: auto;
  margin-right: auto; }

.block {
  margin-top: 2rem;
  margin-bottom: 6rem; }

.center-with-margins {
  max-width: 667px;
  margin-left: auto;
  margin-right: auto; }

.text-large {
  font-size: 1.4rem;
  font-weight: 600;
  max-width: 667px;
  margin-left: auto;
  margin-right: auto; }

.slack-highlight {
  background-color: #FEDCFF; }

.lcbo-highlight {
  background-color: #CCF0DD; }

.ea-highlight {
  background-color: #FFDADA; }

.case-study-img {
  max-width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.case-study-footer {
  text-align: center; }

@media screen and (min-width: 576px) {
  .content {
    margin: 4rem auto 4rem auto;
    padding: 0; }
  .block {
    margin-top: 4rem; }
  .text-large {
    font-size: 2rem; } }

@media screen and (min-width: 768px) {
  .content {
    margin: 6rem auto 6rem auto; }
  .block {
    margin-top: 5rem; } }

@media screen and (min-width: 1300px) {
  .content {
    margin: 8rem auto 6rem auto; }
  .block {
    margin-top: 6rem;
    margin-bottom: 8rem; } }
