.header {
  min-height: 10vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  margin-bottom: 4rem; }

.links {
  position: absolute;
  right: 2rem;
  bottom: 0px; }

.header-link {
  margin: 0rem 0rem 0rem 1rem;
  font-size: 1rem; }

.active-link {
  font-weight: 600;
  color: #3E3E3E; }

.favicon {
  height: 3rem;
  position: absolute;
  left: 2rem;
  margin: 0rem; }

@media screen and (min-width: 576px) {
  .header-link {
    margin: 0rem 0rem 0rem 2rem; } }

@media screen and (min-width: 768px) {
  .header {
    margin-bottom: 2rem; }
  .favicon {
    left: 8rem; }
  .header-link {
    font-size: 1.3rem;
    margin: 0rem 0rem 0rem 4rem; } }
